___CSS_LOADER_EXPORT___.push([module.id, `.review-form {
  padding: 20px;
  background-color: #f2f2f2;
  text-align: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.form-input {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.form-input input,
.form-input textarea {
  width: 100%;
  height: 44px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  margin-bottom: 10px;
}

.form-input textarea {
  height: 100px;
}

.form-input label {
  font-weight: bold;
  margin-bottom: 5px;
}


.form-submit {
  background-color: #55f87b;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

#loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fbfbfb;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
